import { ObservePolicy, ObserverTarget, IWatcher } from './IObserver'
import { GLOBAL } from '../utility/consts'

export default function(): ObservePolicy {
	if (GLOBAL.Proxy)
		return {
			__name: 'Proxy',
			__proxy: true,
			__createProxy(
				target: ObserverTarget,
				isArray: boolean,
				watchers: { [prop: string]: IWatcher }
			): ObserverTarget {
				return new Proxy(target, {
					set: (source, prop: string, value) => {
						let watcher = watchers[prop]
						watcher && watcher.notify(source[prop])
						source[prop] = value
						return true
					}
				})
			}
		}
}
